import app_store from '@/stores/app.store';
import { Component, Host, h } from '@stencil/core';

@Component({
  tag: 'ir-privacy-policy',
  styleUrl: 'ir-privacy-policy.css',
  shadow: true,
})
export class IrPrivacyPolicy {
  dialogRef: HTMLIrDialogElement;

  render() {
    return (
      <Host>
        <ir-button label="privacy policy" variants="link" onButtonClick={() => this.dialogRef.openModal()}></ir-button>
        <ir-dialog ref={el => (this.dialogRef = el)}>
          <h1 slot="modal-title" class=" px-4 pt-4 md:px-6 md:pt-6 text-xl font-semibold">
            Privacy and policy
          </h1>
          <div class="max-h-[90vh] p-4 md:p-6 md:pb-6" slot="modal-body">
            <p innerHTML={app_store.property.privacy_policy}></p>
          </div>
        </ir-dialog>
      </Host>
    );
  }
}
